import React from 'react';
import Header from './Header';
import Footer from './Footer';
import ContUnSlider from './ContUnSlider';


const BancaInternet = () => (
  <div>
    <Header title="hola mundo"/>
    <ContUnSlider listBanks={listBank}/>
    <Footer enlace="Ir a banca por internet"/>
  </div>
);


const listBank = [
          {name:'bcp' ,bank:'https://botw-pd.s3.amazonaws.com/styles/logo-thumbnail/s3/042014/bcp-125anos.ai-converted.png?itok=kiH1e9T-'} , 
          {name:'bbva', bank:'https://botw-pd.s3.amazonaws.com/styles/logo-thumbnail/s3/0002/3894/brand.gif?itok=tPHKhTyN'},
          {name:'scotiabank', bank:'data:image/jpeg;base64,'},
          {name:'interbank',bank:'https://mall-prod.spincorp.cl/uploads/stores/fa2fcf9809e0d69cc68e6f36c264a7ef.png'},
          {name:'banbif', bank:'http://www.hablemosmassimple.com/Files/Media/b8340d9d-6791-4770-a101-2a065e009a37.jpg'}
 ]


    
export default BancaInternet;


